body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

img {
  max-width: 100%;
  width: auto;
  height: auto;
}

body {
  /*background-image: url("../images/background.jpg");*/
  background-image: url(/static/media/background-4.dd2624fb.jpg);
  background-color: #cccccc;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  position: absolute;

  width: 100%;
  height: 100%;
}

.lip-container {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  /*overflow-y: scroll*/
  border-radius: 20px;
  border-width: 1px;
  /*background-color: rgba(58,58,58,0.5);*/
}

.lip-title {
  background-image: url(/static/media/title.28f4ada6.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 150%;
  height: 150%;
}

.dropdown-group {
  text-align: center;
  position: center;
}

.output-container {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

CharacteristicInput {
  margin: 0 auto;
}

BrandInput {
  margin: 0 auto;
}

.form-text {
  text-shadow: 2px 2px 2px black;
  -webkit-text-fill-color: white; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}

.dropdown-toggle {
  width: 200px;
}

.dropdown-menu {
  height: 200px;
  overflow-y: scroll;
}

.dropdown-item {
  width: 230px;
}

.dropdown-1 {
  width: 70px;
  margin-left: -2px;
  margin-right: -2px;
}

.dropdown-2 {
  width: 220px;
}

.cd-fixed-bg {
  min-height: 100vh;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: repeat;
  background-position: center center;
}

.cd-fixed-bg.cd-fixed-bg--1 {
  background-image: url(/static/media/background-4.dd2624fb.jpg);
}

.cd-scrolling-bg {
  min-height: 100%;
}

.spinner {
  font-size: 1.6em
}

.title {
  width: 600px
}

.type-head {
  overflow-y: scroll
}

.page-select {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  position: relative;
  width: 100%;
  margin-left: 25px;
}

.color-input {
  margin-left: 35px;
}

.colorExample {
  width: 25px;
  height: 25px;
  margin-left: 10px;
  float: right;
  border-style: solid;
  border-width: 1px;
  border-color: black;
}

.crown {
  width: 30px;
  height: 18px;
  margin: 0px 5px 5px 5px;
}







